<template>
  <AppLayout>
    <Breadcrumbs />
    <div class="create-account ps-md-20 p-15 pt-0 ">
      <div v-if="!thanksActive">
        <h1>{{ $t("create_new_account") }}</h1>
        <b-form class="create-form col-12 col-md-6" @submit.prevent="formSubmit">
          <!-- <b-form-checkbox id="checkbox-1" class="custom-control custom-checkbox" input-class="custom-control-input"
                v-model="isCompany" name="checkbox-1">{{ $t("form_are_you_a_company") }}</b-form-checkbox> -->

          <h3 class="subtitle">{{ $t("personal_information") }}:</h3>

          <!-- <label>{{ $t('gender') }}:</label>
          <BFormRadioGroup id="gender" :options="genderOptions" name="gender-radio" :label="$t('gender')"
            :label-for="$t('gender')" /> -->

          <b-form-group class="account-inputs" id="gender" :label="$t('gender')" :label-for="$t('gender')">
            <b-form-radio class="account-radios" :state="form.genderState" @blur="validateGender" v-model="gender" v-for="(item, index) in genderOptions" :key="index"
              name="some-radios" :value="item.value" aria-describedby="gender-feedback">
              {{ item.text }}
            </b-form-radio>
            <b-form-invalid-feedback id="gender-feedback">
              {{ form.genderError }}
            </b-form-invalid-feedback>
          </b-form-group>

          <b-form-group class="account-inputs" id="first-name-group" label-for="first_name">
            <b-form-input :placeholder="$t('form_first_name')" id="first_name" v-model="form.firstname" type="text"
              @blur="validateFirstname" :state="form.firstnameState"
              aria-describedby="firstname-feedback"></b-form-input>
            <b-form-invalid-feedback id="firstname-feedback">
              {{ form.firstnameError }}
            </b-form-invalid-feedback>
          </b-form-group>

          <b-form-group class="account-inputs" id="last-name-group" label-for="last_name">
            <b-form-input :placeholder="$t('form_last_name')" id="last_name" v-model="form.lastname" type="text"
              @blur="validateLastname" :state="form.lastnameState" aria-describedby="lastname-feedback"></b-form-input>
            <b-form-invalid-feedback id="lastname-feedback">
              {{ form.lastnameError }}
            </b-form-invalid-feedback>
          </b-form-group>

          <!-- <b-form-group class="account-inputs" id="coc_number-group" label-for="coc_number">
            <b-form-input :placeholder="$t('form_coc_number')" id="coc_number" v-model="form.coc_number" type="text"
              @blur="validateCOCNumber" :state="form.coc_numberState"
              aria-describedby="coc_number-feedback"></b-form-input>
            <b-form-invalid-feedback id="coc_number-feedback">
              {{ form.coc_numberError }}
            </b-form-invalid-feedback>
          </b-form-group> -->

          <b-form-group class="account-inputs" id="taxvat-group">
            <b-form-input :placeholder="$t('form_vat_number')" id="taxvat" v-model="form.taxvat" type="text"
              :state="form.taxvatState" aria-describedby="taxvat-feedback"></b-form-input>
            <b-form-invalid-feedback id="taxvat-feedback">
              {{ form.taxvatError }}
            </b-form-invalid-feedback>
          </b-form-group>

          <h3 class="subtitle">{{ $t("address_information") }}:</h3>

          <b-form-group class="account-inputs" id="company-group" label-for="company">
            <b-form-input :placeholder="$t('form_company_name')" id="company" v-model="form.company" type="text"
              @blur="validateCompany" :state="form.companyState" aria-describedby="company-feedback"></b-form-input>
            <b-form-invalid-feedback id="company-feedback">
              {{ form.companyError }}
            </b-form-invalid-feedback>
          </b-form-group>

          <b-row class="g-3">
            <b-col md="4">
              <b-form-group class="account-inputs" id="country-group" label-for="country">
                <b-form-select id="country" v-model="form.address.country_code" :options="countries"
                  class="select"></b-form-select>
              </b-form-group>
            </b-col>

            <b-col md="4">
              <b-form-group class="account-inputs" id="postcode-group" label-for="postcode">
                <b-form-input @blur="validatePostcode" :placeholder="$t('form_postal_code')" id="postcode"
                  v-model="form.address.postcode" type="text" :state="form.address.postcodeState"
                  aria-describedby="address.postcode-feedback"></b-form-input>
                <b-form-invalid-feedback id="address.postcode-feedback">
                  {{ form.address.postcodeError }}
                </b-form-invalid-feedback>
              </b-form-group>
            </b-col>

            <b-col md="4">
              <b-form-group class="account-inputs" id="house_number-group" label-for="house_number">
                <b-form-input :placeholder="$t('form_house_number')" id="house_number"
                  v-model="form.address.house_number" type="text" @blur="validateHouseNumber"
                  :state="form.address.house_numberState"
                  aria-describedby="address.house_number-feedback"></b-form-input>
                <b-form-invalid-feedback id="address.house_number-feedback">
                  {{ form.address.house_numberError }}
                </b-form-invalid-feedback>
              </b-form-group>
            </b-col>
          </b-row>

          <b-form-group v-if="showFullAddress" class="account-inputs" id="street-group" label-for="street">
            <b-form-input :placeholder="$t('form_street')" id="street" v-model="form.address.streetDisplay" type="text"
              @blur="validateStreet" :state="form.address.streetDisplayState"
              aria-describedby="address.streetDisplay-feedback"></b-form-input>
            <b-form-invalid-feedback id="address.streetDisplay-feedback">
              {{ form.address.streetDisplayError }}
            </b-form-invalid-feedback>
          </b-form-group>

          <!-- <b-form-group class="account-inputs" id="addition-group">
              <b-form-input :placeholder="$t('form_addition')" id="addition" v-model="form.address.addition"
                type="text"></b-form-input>
            </b-form-group> -->

          <b-form-group v-if="showFullAddress" class="account-inputs" id="city-group" label-for="city">
            <b-form-input :placeholder="$t('form_city')" id="city" v-model="form.address.city" type="text"
              @blur="validateCity" :state="form.address.cityState"
              aria-describedby="address.city-feedback"></b-form-input>
            <b-form-invalid-feedback id="address.city-feedback">
              {{ form.address.cityError }}
            </b-form-invalid-feedback>
          </b-form-group>
          <b-form-group v-if="foundAddress != '' && showFullAddress == false" class="account-inputs" id="add-group"
            label=" " label-for="add">
            <div id="add">
              {{ foundAddress }}
            </div>
          </b-form-group>

          <b-form-group v-if="foundAddresses.length > 0 && showFullAddress == false" class="account-inputs"
            id="addm-group" label=" " label-for="addm">
            <div id="addm">
              <b-form-radio style="width: 300px" class="account-radios" v-model="selectedAddress"
                v-for="(item, index) in foundAddresses" :key="index" name="some-radios" :value="index">{{ item.text
                }}</b-form-radio>
            </div>
          </b-form-group>
          <b-form-checkbox v-if="showManual" id="manual" v-model="showFullAddress" name="manual"
            class="custom-control custom-checkbox" input-class="custom-control-input">{{
              $t("form_fillout_address_manual") }}</b-form-checkbox>
          <b-form-group class="account-inputs" id="telephone-group" label-for="telephone">
            <b-form-input :placeholder="$t('form_phone')" id="telephone" v-model="form.address.telephone" type="text"
              @blur="validatePhone" :state="form.address.telephoneState"
              aria-describedby="address.telephone-feedback"></b-form-input>
            <b-form-invalid-feedback id="address.telephone-feedback">
              {{ form.address.telephoneError }}
            </b-form-invalid-feedback>
          </b-form-group>

          <h3 class="subtitle">{{ $t("login_details") }}</h3>
          <!-- Login details section start -->
          <b-form-group class="account-inputs" id="email-group" label-for="email">
            <b-form-input :placeholder="$t('form_email')" id="email" v-model="form.email" type="text"
              :state="form.emailState" aria-describedby="email-feedback"></b-form-input>
            <b-form-invalid-feedback id="email-feedback">
              {{ form.emailError }}
            </b-form-invalid-feedback>
          </b-form-group>

          <b-form-group class="account-inputs" id="password-group" label-for="password">
            <b-form-input :placeholder="$t('form_password')" id="password" v-model="form.password" type="password"
              :state="form.passwordState" aria-describedby="password-feedback"></b-form-input>
            <password-meter v-if="form.password && isClient" :password="form.password" />
            <b-form-invalid-feedback id="password-feedback">
              {{ form.passwordError }}
            </b-form-invalid-feedback>
          </b-form-group>

          <b-form-group class="account-inputs" id="password_confirm-group" label-for="password_confirm">
            <b-form-input :placeholder="$t('form_password_confirmation')" id="password_confirm"
              v-model="form.password_confirm" type="password" :state="form.password_confirmState"
              aria-describedby="password_confirm-feedback"></b-form-input>
            <b-form-invalid-feedback id="password_confirm-feedback">
              {{ form.password_confirmError }}
            </b-form-invalid-feedback>
          </b-form-group>

          <!-- Login details section over -->

          <b-form-checkbox id="checkbox-2" name="checkbox-2" label-for="checkbox2"
            v-model="form.address.default_billing" class="custom-control custom-checkbox"
            input-class="custom-control-input">
            {{ $t("form_address_also_invoice_address") }}
          </b-form-checkbox>
          <b-form-checkbox id="checkbox-3" name="checkbox-3" label-for="checkbox3" v-model="form.is_subscribed"
            class="custom-control custom-checkbox" input-class="custom-control-input">
            {{ $t("form_newsletter_subscription") }}
          </b-form-checkbox>
          <!-- </b-col> -->
          <div class="btn-wrap text-center">
            <b-button type="submit" variant="secondary" class="text-white form-submit-btn">
              {{ $t("create_my_account") }}
            </b-button>
          </div>
          <!-- </b-row> -->
        </b-form>
      </div>

      <CreateAccountThanks v-if="thanksActive" />
    </div>
  </AppLayout>
</template>

<script setup>
import { ref, computed, watch, onMounted } from "vue";
import { useRouter } from "vue-router";
import Breadcrumbs from "@/esf_thehague_schinsleder/core/components/Breadcrumbs";
import AppLayout from "@/esf_thehague_schinsleder/core/components/AppLayout";
import CreateAccountThanks from "@/base/core/components/authentication/sign-up/CreateAccountThanks";
import config from "@config";
import PasswordMeter from "vue-simple-password-meter";
import {
  // validatePostcode,
  validateVatNumber,
  checkEmailExist,
} from "@storefront/core/data-resolver/user";
import { useI18n } from "vue-i18n";
import { useHead } from "@unhead/vue";
import { useStore } from "vuex";

const store = useStore();
const router = useRouter();
const { t } = useI18n();

const isClient = ref(false);
// const isCompany = ref(false);
const thanksActive = ref(false);
const showFullAddress = ref(true);
const showManual = ref(true);
const typeTimer = ref(null);
const foundAddresses = ref([]);
const foundAddress = ref("");
const selectedAddress = ref(null);
const isValidVatNumber = ref(false);
const showVatMessage = ref("");
const showVatCheck = ref(false);
const emailReg = ref(
  /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/
);

const form = ref({
  email: "",
  emailState: null,
  emailError: "",
  emailTimer: null,
  password: "",
  passwordState: null,
  passwordError: "",
  passwordTimer: null,
  password_confirm: "",
  password_confirmState: null,
  password_confirmError: "",
  password_confirmTimer: null,
  company: "",
  companyState: null,
  companyError: "",
  companyTimer: null,
  gender: "",
  genderState: null,
  genderError: "",
  genderTimer: null,
  firstname: "",
  firstnameState: null,
  firstnameError: "",
  firstnameTimer: null,
  lastname: "",
  lastnameState: null,
  lastnameError: "",
  lastnameTimer: null,
  // coc_number: "",
  // coc_numberState: null,
  // coc_numberError: "",
  // coc_numberTimer: null,
  taxvat: "",
  taxvatState: null,
  taxvatError: "",
  taxvatTimer: null,
  address: {
    country_code: "",
    postcode: "",
    postcodeState: null,
    postcodeError: "",
    postcodeTimer: null,
    street: "",
    streetDisplay: "",
    streetDisplayState: null,
    streetDisplayError: "",
    streetDisplayTimer: null,
    house_number: "",
    house_numberState: null,
    house_numberError: "",
    house_numberTimer: null,
    addition: "",
    city: "",
    cityState: null,
    cityError: "",
    cityTimer: null,
    telephone: "",
    telephoneState: null,
    telephoneError: "",
    telephoneTimer: null,
    default_billing: true,
    default_shipping: true,
  },
  is_subscribed: true,
  checkbox2: false,
  manual: false,
});

const genderOptions = [
  { text: t("man"), value: t("man"), },
  { text: t("woman"), value: t("woman"), },
  { text: t("other"), value: t("other"), },
];
const countries = computed(() => store.getters["user/getCountries"]);

const country = computed(() => form.value.address.country_code);
const postcodeHouseNumber = computed(
  () => `${form.value.address.postcode}|${form.value.address.house_number}`
);
const vatNumber = computed(() => form.value.taxvat);
const Email = computed(() => form.value.email);
const Password = computed(() => form.value.password);
const PasswordConfirm = computed(() => form.value.password_confirm);
const isLoggedIn = computed(() => store.getters["user/getIsLoggedIn"]);

watch(isLoggedIn, (newValue) => {
  if (newValue) {
    router.push({ name: "account" });
  }
});

watch(Email, () => {
  if (form.value.emailTimer !== null) {
    clearTimeout(form.value.emailTimer);
  }
  form.value.emailTimer = setTimeout(() => validateEmail(), 1000);
});

watch(Password, () => {
  validatePassword();
});

watch(PasswordConfirm, () => {
  validateConfirmationPassword();
});

watch(country, () => {
  if (config.postcode_validation.enabled) {
    if (!config.postcode_validation.countries.includes(country.value)) {
      showFullAddress.value = true;
    } else {
      showFullAddress.value = false;
    }
  } else {
    showFullAddress.value = true;
  }
});

watch(vatNumber, () => {
  if (config.vat_validation.enabled) {
    if (form.value.taxvatTimer !== null) {
      clearTimeout(form.value.taxvatTimer);
    }
    form.value.taxvatTimer = setTimeout(() => validateVAT(), 1000);
  }
});

watch(postcodeHouseNumber, (newVal) => {
  if (showFullAddress.value == false) {
    if (typeTimer.value !== null) {
      clearTimeout(typeTimer.value);
    }
    if (foundAddress.value != "") {
      foundAddress.value = "";
    }
    if (foundAddresses.value.length > 0) {
      foundAddresses.value = [];
    }
    const [newPostcode, newHouseNumber] = newVal.split("|");
    if (newPostcode != "" && newHouseNumber != "") {
      typeTimer.value = setTimeout(() => validatePostcodeHouseNumber(), 2000);
    }
  }
});

watch(selectedAddress, (newVal) => {
  form.value.address.streetDisplay = foundAddresses.value[newVal].street;
  form.value.address.addition = foundAddresses.value[newVal].letter ?? "";
  form.value.address.city = foundAddresses.value[newVal].city;
  validatePostcode();
});

const validateEmail = async () => {
  if (form.value.email.length == 0) {
    form.value.emailState = false;
    form.value.emailError = t("email_is_required_field");
    return false;
  }
  if (!emailReg.value.test(form.value.email)) {
    form.value.emailState = false;
    form.value.emailError = t("email_is_invalid_field");
    return false;
  }
  if (!(await checkEmailExist(form.value.email))) {
    form.value.emailState = false;
    form.value.emailError = t("email_already_exists");
    return false;
  }
  form.value.emailState = true;
  return true;
};

const validatePostcodeHouseNumber = async () => {
  if (!showFullAddress.value) {
    const list = await validatePostcode(
      form.value.address.postcode,
      form.value.address.house_number
    );
    if (list.length === 1) {
      let address = list[0].street + " " + list[0].number;
      form.value.address.streetDisplay = list[0].street;
      if (list[0].letter != null) {
        form.value.address.addition = list[0].letter;
        address = address + list[0].letter;
      } else {
        form.value.addition = "";
      }

      address = address + ", " + list[0].postcode + " " + list[0].city;

      form.value.address.city = list[0].city;
      form.value.address.cityState = true;
      foundAddress.value = address;
      validatePostcode();
    } else if (list.length === 0) {
      foundAddress.value = t("postcode_not_found");
    } else {
      list.forEach(function (item) {
        let address = item.street + " " + item.number;
        if (item.letter != null) {
          address = address + item.letter;
        }

        address = address + ", " + item.postcode + " " + item.city;
        item.text = address;
        foundAddresses.value.push(item);
      });
    }
  } else {
    if (form.value.address.postcode === "") {
      form.value.address.postcodeState = false;
      form.value.address.postcodeError = t("postcode_required");
    } else {
      form.value.address.postcodeState = true;
    }
  }
};

const formSubmit = async () => {
  let formValid = true;

  if (!form.value.emailState) {
    validateEmail();
    if (!form.value.emailState) {
      formValid = false;
    }
  }

  if (!form.value.passwordState) {
    validatePassword();
    if (!form.value.passwordState) {
      formValid = false;
    }
  }

  if (!form.value.password_confirmState) {
    validateConfirmationPassword();
    if (!form.value.password_confirmState) {
      formValid = false;
    }
  }

  // if (isCompany.value) {
  if (!form.value.companyState) {
    validateCompany();
    if (!form.value.companyState) {
      formValid = false;
    }
  }
  // if (!form.value.coc_numberState) {
  //   validateCocNumber();
  //   if (!form.value.coc_numberState) {
  //     formValid = false;
  //   }
  // }
  if (!form.value.taxvatState) {
    validateVAT();
    if (!form.value.taxvatState) {
      formValid = false;
    }
  }
  // }

  if (!form.value.genderState) {
    validateGender();
    if (!form.value.genderState) {
      formValid = false;
    }
  }

  if (!form.value.firstnameState) {
    validateFirstname();
    if (!form.value.firstnameState) {
      formValid = false;
    }
  }

  if (!form.value.lastnameState) {
    validateLastname();
    if (!form.value.lastnameState) {
      formValid = false;
    }
  }

  if (!form.value.address.postcodeState) {
    validatePostcode();
    if (!form.value.address.postcodeState) {
      formValid = false;
    }
  }

  if (!form.value.address.house_numberState) {
    validateHouseNumber();
    if (!form.value.address.house_numberState) {
      formValid = false;
    }
  }

  if (!form.value.address.streetDisplayState) {
    validateStreet();
    if (!form.value.address.streetDisplayState) {
      formValid = false;
      showFullAddress.value = true;
    }
  }

  if (!form.value.address.telephoneState) {
    validatePhone();
    if (!form.value.address.telephoneState) {
      formValid = false;
    }
  }

  if (!form.value.address.cityState) {
    validateCity();
    if (!form.value.address.cityState) {
      formValid = false;
    }
  }

  if (formValid) {
    form.value.address.street = form.value.address.streetDisplay;
    if (form.value.address.house_number !== "") {
      form.value.address.street += " " + form.value.address.house_number;
    }
    if (form.value.address.addition !== "") {
      form.value.address.street += " " + form.value.address.addition;
    }
    // if (!isCompany.value) {
    form.value.company = "";
    form.value.taxvat = "";
    // form.value.coc_number = "";
    // }
    const retval = await store.dispatch("user/createAccount", {
      account: form.value,
      store,
    });
    if (retval) {
      const msg = {
        type: "success",
        title: "account_create",
        text: t("account_created"),
      };
      store.dispatch("messages/sendMessage", { message: msg });
      router.push("/login");
    }
  }
};

const validatePassword = () => {
  if (form.value.password.length < 8) {
    form.value.passwordState = false;
    form.value.passwordError = t("password_needs");
  } else if (!/[a-z]/.test(form.value.password)) {
    form.value.passwordState = false;
    form.value.passwordError = t("password_needs");
  } else if (!/[!"#$%&'()*+,-.:;<=>?@[\]^_`{|}~]/.test(form.value.password)) {
    form.value.passwordState = false;
    form.value.passwordError = t("password_needs");
  } else if (!/[A-Z]/.test(form.value.password)) {
    form.value.passwordState = false;
    form.value.passwordError = t("password_needs");
  } else if (!/\d/.test(form.value.password)) {
    form.value.passwordState = false;
    form.value.passwordError = t("password_needs");
  } else {
    form.value.passwordState = true;
  }
};

const validateConfirmationPassword = () => {
  if (form.value.password != form.value.password_confirm) {
    form.value.password_confirmState = false;
    form.value.password_confirmError = t("password_confirm_not_equal");
  } else {
    form.value.password_confirmState = true;
  }
};

const validateVAT = async () => {
  // if (isCompany.value) {
  if (form.value.taxvat != "") {
    if (
      form.value.taxvat.substring(0, 2).toUpperCase() ===
      form.value.address.country_code
    ) {
      const retval = await validateVatNumber(form.value.taxvat);
      if (retval.valid == "true") {
        isValidVatNumber.value = true;
        form.value.taxvatState = true;
      } else {
        isValidVatNumber.value = false;
        form.value.taxvatState = false;
        form.value.taxvatError = t("taxvat_invalid");
        showVatMessage.value = t("taxvat_invalid");
      }
    } else {
      isValidVatNumber.value = false;
      form.value.taxvatState = false;
      form.value.taxvatError = t("taxvat_wrong_country");
    }
  } else {
    form.value.taxvatState = null;
    form.value.taxvatError = "";
  }
  // } else {
  //   form.value.taxvatState = null;
  //   form.value.taxvatError = "";
  // }
};

const validateCompany = () => {
  // if (isCompany.value) {
  if (form.value.company == "") {
    form.value.companyState = false;
    form.value.companyError = t("company_name_required");
  } else {
    form.value.companyState = true;
  }
  // } else {
  //   form.value.companyState = null;
  // }
};

const validateGender = () => {
  if (form.value.gender == "") {
    form.value.genderState = false;
    form.value.genderError = t("gender_required");
  } else {
    form.value.genderState = true;
  }
};

const validateFirstname = () => {
  if (form.value.firstname == "") {
    form.value.firstnameState = false;
    form.value.firstnameError = t("firstname_required");
  } else {
    form.value.firstnameState = true;
  }
};

const validateLastname = () => {
  if (form.value.lastname == "") {
    form.value.lastnameState = false;
    form.value.lastnameError = t("lastname_required");
  } else {
    form.value.lastnameState = true;
  }
};

